<template>
  <div v-bind:class="{'small-box' :s , ios:i}">
    <div v-if="title ||url">
      <header class="header">
        <a v-bind:href="url">{{title}}</a>
      </header>
      <hr />
    </div>
    <slot name="main"></slot>
  </div>
</template>

<script>
export default {
  props: ["title", "url"],
  mounted() {
    const ua = navigator.userAgent;
    const isIOS =
      ua.indexOf("iPhone") >= 0 ||
      ua.indexOf("iPad") >= 0 ||
      navigator.userAgent.indexOf("iPod") >= 0;
    if (isIOS) {
      this.i = true;
    }
  },
  data() {
    return {
      i: false,
      s: true
    };
  }
};
</script>

<style scoped>
.small-box {
  border: solid 1px gray;
  margin: 5px;
  display: inline-block;
  min-width: 100%;
}
.header {
  font-size: 200%;
}
.ios {
  width: 1000px;
}
</style>


