<template>
  <div class="big-box">
    <header class="header">{{title}}</header>
    <div class="slot">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default{
    props : ["title"]
}
</script>

<style>
.big-box {
  border: solid 1px;
  margin:5px;
  display: inline-block;
  min-width:100%;
}
.header{
    font-size : 30px;
}
.slot {
  padding: 5px;
  margin: auto;
}
</style>


